// 
var toggle = '[data-toggle="filter"]'

/**
 * 
 * @param  {RegExp}   regexp   
 * @param  {Function} callback 
 * @return {Boolean}  
 */
function inputFilter(regexp, callback) {
  return function(event) {
    var chr;
    var e = event || window.event;

    if(e.ctrlKey || e.altKey || e.metaKey) return;

    chr = getChar(e);

    if(chr === null) return;

    if(!regexp.test(chr)) return false;

    typeof(callback) === 'function' && callback.call(chr)
  }

  function getChar(event) {
    if(event.which === null) {
      if(event.keyCode < 32) {
        return null;
      }

      return String.fromCharCode(event.keyCode)
    }

    if(event.which !== 0 && event.charCode !== 0) {
      if(event.which < 32) return null;

      return String.fromCharCode(event.which);
    }

    return null;
  }
}


/**
 * 
 * @type {Object}
 */
var patterns = {
  number: /^[0-9]+$/,  // 
  integer: /^\-?[0-9]+$/, //
  decimal: /^\-?[0-9]*\.?[0-9]+$/, //
  natural: /^[1-9][0-9]*$/i, // 0
  letter: /[a-zA-z]/,  // 
  email: /[a-z0-9_\.\-@]/i, // 
  hex: /[0-9a-f]/i,  // 
  alpha: /^[a-z]+$/i, // 
  alphaNum: /[a-z0-9_]/i, // 
  name: /[a-zA-Z0-9_\-]/,  // 
  phone: /\d{3,4}-\d{5,}/, //  020(0668)-5201314
  mobile: /^1[3-9]\d{9}/,   //  1[3-9]xxxxxxxxx
  ip: /^((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){3}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})$/i, // ip
  base64: /[^a-zA-Z0-9\/\+=]/i,
  url: /^((http|https):\/\/(\w+:{0,1}\w*@)?(\S+)|)(:[0-9]+)?(\/|\/([\w#!:.?+=&%@!\-\/]))?$/,
  date: /\d{4}-\d{1,2}-\d{1,2}/, // 
  chinese: /^[\u4e00-\u9fa5]+$/, // 
  nickName: /^[\u4e00-\u9fa50-9a-zA-Z_\-]+$/  // 
}

/**
 * 
 * @param  {Object} el 
 * @return RegExp 
 */
function getPattern (el) {
  var pattern = el.getAttribute('pattern');

  if(!pattern) return null;
  var p = patterns[pattern];

  if(!p) { // pattern 
    p = new RegExp(pattern);
  }

  return p;
}

/**
 * 
 * @param  {Event} event keypress
 * @return {Boolean}  true - , false
 */
function handleKeyPressEvent(event) {
  var pattern = getPattern(event.target);

  return pattern && inputFilter(pattern)(event);
}

/**
 * keyup 
 * @param  {Event} event keypress
 * @return {Boolean}  true - , false
 */
function handleKeyAfterEvent(event) {
  var pattern = getPattern(event.target);

  if(pattern === null) return;
  var $this = $(this), defaultVal = $this.val(), matched = false;
  setTimeout(function(){
    $this.val(function() {
      var res = defaultVal.replace(pattern, function(match, index, str) {
        matched = true;
        return match
      })
      return matched ? res : '';
    })
  }, 1);
}


// ----------
// jQuery 
$(function() {
  $(document).on('keypress', toggle, handleKeyPressEvent)
  $(document).on('paste change propertychange', toggle, handleKeyAfterEvent)
})

module.exports = {
  filter: inputFilter,
  keyPress: handleKeyPressEvent,
  keyAfter: handleKeyAfterEvent
}



